import React, { PropsWithChildren, useEffect, useState } from 'react';

type UserLocation = {
  country: string;
  timezone: string;
};

export const UserLocationContext = React.createContext<UserLocation | null>(null);

export const UserLocationProvider: React.FC<PropsWithChildren> = ({ children }) => {
  const [country, setCountry] = useState('');
  const [timezone, setTimezone] = useState('');

  const fetchUserLocation = async () => {
    const response = await fetch('http://ip-api.com/json');
    const location = await response.json();

    if (location && location.status === 'success') {
      setCountry(location.countryCode);
      setTimezone(location.timezone);
    }
  };

  useEffect(() => {
    fetchUserLocation();
  }, []);

  return (
    <UserLocationContext.Provider value={{ country, timezone }}>
      {children}
    </UserLocationContext.Provider>
  );
};
